#map {
  width: 100%;
  height: 500px;
}

.map {
  width: 100%;
  background-color: var(--greeny-blue);

  &__title {
    text-align: center;
    padding-top: 20px;
    position: relative;
    margin: 0;
  }

  &__text {
    z-index: 10;
    text-align: center;
    position: relative;
    left: 0px;
  }
}
