.parallax {
	position: relative;
	height: 100%;
}

.header {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 1049px;
  background-image: url(../img/fog.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  z-index: 99;


  &__container {
    display: flex;
    width: 76%;
    padding: 0;
  }

  &__text {
    align-self: center;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 25px;
    font-weight: normal;
    line-height: normal;
    text-align: center;
		margin: -2px;
		margin-top: -10px;

    @include touchScreen {
      font-size: 17px;
      margin: -12px;
    }
	}
	
	&__parallax-fog {
		background: url('../img/fog_1.png') bottom / 100% no-repeat;
		position: fixed;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 2;
		pointer-events: none;
	}
}

.search-form {
  width: 100%;
  position: relative;
	top: 920px;
  //margin-top: 185px;
  @include touchScreen {
    top: 193px;
  }


  &__list {
    background-color: var(--color-white);
    padding: 60px 79px 57px 83px;
    display: flex;

    @include touchScreen {
      padding: 20px 29px 17px 29px;
    }
  }

  &__item {
    width: 25%;
    height: auto;
    border: 2px solid var(--brownish-grey-43);
    text-align: center;
    vertical-align: middle;

    &:first-child {
      border-top-left-radius: 12px;
      border-bottom-left-radius: 12px;
    }

    &:last-child {
      background-color: var(--greeny-blue);
      border-top-right-radius: 12px;
      border-bottom-right-radius: 12px;
    }

    &:not(:first-child) {
      border-left: 0px;
    }
  }

  &__link {
    padding: 26px 0px;
    display: inline-block;
    text-transform: uppercase;
    color: var(--brownish-grey-43);
    font-size: 23.5px;
    font-weight: bold;

    @include touchScreen {
      font-size: 17.5px;
      padding: 18px 0px;
    }
  }

  &__btn {
    color: var(--color-white);
    background-color: transparent;
  }
}

.logo {
  position: absolute;
  left: 45%;
  transform: translate(-43%, -50%);
  top: 50%;
  display: inline-block;
  min-width: 890px;
  margin-top: -28px;
  color: var(--color-white);
  font-family: "Arkhip", sans-serif;
  font-weight: normal;
  font-size: 201px;
  line-height: 202px;
  letter-spacing: -9.19px;
  text-align: center;


  @include touchScreen {
    font-size: 151px;
  }

  &::before {
    content: "";
    position: absolute;
    right: -78px;
    bottom: 39px;
    width: 22%;
    height: 14%;
    background-image: url(../img/whitewave.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 80%;
    object-fit: cover;

    @include touchScreen {
      background-size: 66%;
      right: 36px;
      bottom: 51px;
    }
  }
}

