.extra {
  padding: 108px 0 0 0px;
  background-image: url("../img/sauna.jpg");
  background-position: 0px 0px;
  background-repeat: no-repeat;
  background-size: cover;

  &__container {
    position: relative;
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    width: 76%;
    padding: 0;
  }

  &__title {
    display: block;
    word-wrap: break-word;
    width: 16%;
    margin: 13px 265px 0px 0px;
    text-align: left;
    height: 300px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 60%;
    max-height: 500px;
    padding-top: 10px;
    margin-bottom: 147px;
  }

  &__items {
    position: relative;
    display: block;
    padding: 0px 66px 70px 0px;
    width: 50%;
    margin-right: 111px;
    text-transform: uppercase;
    font-family: 'Corbel', sans-serif;
    font-size: 31px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: var(--color-white);

    &:nth-child(4n) {
      white-space: nowrap;
    }

    &:nth-child(6n) {
      white-space: nowrap;
    }
  }

  &__svg {
    z-index: 100;
    position: absolute;
    left: -64px;
    bottom: 71px;
    height: 41px;
    width: 44px;
    object-fit: cover;

    &-hunny {
      bottom: 98px;
    }

    &-sheet {
      bottom: 173px;
      left: -59px;
    }

    &-samovar {
      bottom: 96px;
      left: -59px;
    }

    &-services {
      left: -59px;
    }
  }

  &__flower {
    position: absolute;
    bottom: 0;

    &_midi {
      height: 24.2%;
      left: 79px;
    }

    &_small {
      height: 27.5%;
      left: -12px;
    }

    &_bigi {
      height: 38%;
      left: 39px;
    }
  }

}
