body {
  margin: 0;
  font-family: "Arkhip", sans-serif;
  font-weight: normal;
  color: var(--color-white);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 768px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title-strong {
  text-transform: uppercase;
  font-size: 53px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -1.87px;
  color: var(--greeny-blue);
}

.parallax {
  position: relative;
  height: 100%;
}

.header {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 1049px;
  background-image: url(../img/fog.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  z-index: 99;
}

.header__container {
  display: flex;
  width: 76%;
  padding: 0;
}

.header__text {
  align-self: center;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 25px;
  font-weight: normal;
  line-height: normal;
  text-align: center;
  margin: -2px;
  margin-top: -10px;
}

@media (max-width: 1024px) {
  .header__text {
    font-size: 17px;
    margin: -12px;
  }
}

.header__parallax-fog {
  background: url("../img/fog_1.png") bottom/100% no-repeat;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  pointer-events: none;
}

.search-form {
  width: 100%;
  position: relative;
  top: 920px;
}

@media (max-width: 1024px) {
  .search-form {
    top: 193px;
  }
}

.search-form__list {
  background-color: var(--color-white);
  padding: 60px 79px 57px 83px;
  display: flex;
}

@media (max-width: 1024px) {
  .search-form__list {
    padding: 20px 29px 17px 29px;
  }
}

.search-form__item {
  width: 25%;
  height: auto;
  border: 2px solid var(--brownish-grey-43);
  text-align: center;
  vertical-align: middle;
}

.search-form__item:first-child {
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}

.search-form__item:last-child {
  background-color: var(--greeny-blue);
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}

.search-form__item:not(:first-child) {
  border-left: 0px;
}

.search-form__link {
  padding: 26px 0px;
  display: inline-block;
  text-transform: uppercase;
  color: var(--brownish-grey-43);
  font-size: 23.5px;
  font-weight: bold;
}

@media (max-width: 1024px) {
  .search-form__link {
    font-size: 17.5px;
    padding: 18px 0px;
  }
}

.search-form__btn {
  color: var(--color-white);
  background-color: transparent;
}

.logo {
  position: absolute;
  left: 45%;
  transform: translate(-43%, -50%);
  top: 50%;
  display: inline-block;
  min-width: 890px;
  margin-top: -28px;
  color: var(--color-white);
  font-family: "Arkhip", sans-serif;
  font-weight: normal;
  font-size: 201px;
  line-height: 202px;
  letter-spacing: -9.19px;
  text-align: center;
}

@media (max-width: 1024px) {
  .logo {
    font-size: 151px;
  }
}

.logo::before {
  content: "";
  position: absolute;
  right: -78px;
  bottom: 39px;
  width: 22%;
  height: 14%;
  background-image: url(../img/whitewave.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80%;
  object-fit: cover;
}

@media (max-width: 1024px) {
  .logo::before {
    background-size: 66%;
    right: 36px;
    bottom: 51px;
  }
}

.snowmobiles__services {
  margin-top: 0px;
}

.snowmobiles__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 76%;
  padding: 0;
  margin-top: 35px;
}

.snowmobiles__promotext {
  position: relative;
  margin-top: 83px;
  width: 70%;
  align-self: start;
}

.snowmobiles__promo {
  width: 37%;
  position: relative;
  margin-top: 100px;
}

.snowmobiles__promo::after {
  z-index: 100;
  position: absolute;
  content: "";
  left: -959px;
  bottom: 15px;
  height: 100px;
  width: 240px;
  background-image: url(../img/wave.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 97%;
  object-fit: cover;
}

.snowmobiles__lead {
  text-align: left;
  font-size: 24px;
  margin-bottom: 39px;
}

.snowmobiles__img {
  display: block;
  position: relative;
}

.snowmobiles__slogan {
  position: relative;
  display: block;
  width: 79%;
  font-family: "Arkhip", sans-serif;
  font-weight: normal;
  font-size: 54px;
  text-align: left;
  letter-spacing: -1.87px;
  color: var(--color-black);
}

.snowmobiles__slogan::after {
  z-index: 100;
  position: absolute;
  content: "";
  left: -95px;
  top: -11px;
  height: 100px;
  width: 200px;
  background-image: url(../img/wave.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 37%;
  object-fit: cover;
}

.snowmobiles__text {
  font-family: "Corbel", san-serif;
  color: var(--brownish-grey);
  font-size: 21px;
  text-align: left;
  display: block;
  line-height: 28px;
  width: 60%;
  margin: 0;
  margin-top: 28px;
}

.snowmobiles__descr {
  font-family: 'Arial' , sans-serif;
  width: 80%;
  display: flex;
  flex-direction: column;
  background-color: var(--color-black);
  position: absolute;
  left: -280px;
  top: 31%;
  border-radius: 14px;
  z-index: 2;
}

.snowmobiles__title {
  font-size: 21px;
  padding: 0;
  margin: 0;
  display: block;
  padding-top: 40px;
  padding-left: 105px;
  text-transform: uppercase;
}

.snowmobiles__items {
  margin: 0;
  margin-bottom: 60px;
  font-size: 19px;
}

.snowmobiles__item {
  font-size: 20px;
  display: block;
  padding: 22px 30px 0 105px;
  line-height: 22px;
  letter-spacing: 0.55px;
}

.snowmobiles__item:first-child {
  padding-top: 41px;
}

.gallary-forest {
  z-index: -1;
  margin-top: -235px;
  padding-bottom: 29px;
  background-color: var(--light-grey);
}

.gallary-forest__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 76%;
  padding: 0;
}

.gallary-forest__slogan {
  font-family: "Arkhip", sans-serif;
  display: block;
  margin-top: 366px;
  padding: 0 100px 0;
  font-size: 36px;
  letter-spacing: -1.26px;
  color: var(--almost-black);
  align-self: center;
}

.slider {
  margin-top: 10px;
  position: relative;
  display: block;
  padding: 0 0px;
  /* Активная точка */
  /* Элемент точки */
}

.slider__item {
  min-width: 490px;
  text-align: center;
  padding: 20px 15px;
  height: 340px;
  opacity: 0.75;
}

.slider__item .slick-center {
  opacity: 1;
}

.slider__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  font-size: 0;
}

.slider .slick-arrow.slick-prev {
  left: 0;
}

.slider .slick-arrow.slick-next {
  right: 0;
}

.slider .slick-track {
  display: flex;
}

.slider .slick-list {
  overflow: hidden;
  margin-left: -5px;
  margin-right: -5px;
}

.slider .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider .slick-dots li {
  list-style: none;
  margin: 0px 10px;
}

.slider .slick-dots li.slick-active button {
  background-color: transparent;
  border: 2px solid #46bd97;
  cursor: pointer;
}

.slider .slick-dots li button:before {
  content: '';
}

.slider .slick-dots button {
  font-size: 0;
  width: 16px;
  height: 16px;
  background-color: var(--color-white);
  border-radius: 50%;
  cursor: pointer;
}

.slider .slick-arrow.slick-disabled {
  opacity: 0.15;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../node_modules/slick-carousel/slick/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("../node_modules/slick-carousel/slick/fonts/slick.eot");
  src: url("../node_modules/slick-carousel/slick/fonts/slick.eot?#iefix") format("embedded-opentype"), url("../node_modules/slick-carousel/slick/fonts/slick.woff") format("woff"), url("../node_modules/slick-carousel/slick/fonts/slick.ttf") format("truetype"), url("../node_modules/slick-carousel/slick/fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: black;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}

