.snowmobiles {

	&__services {
		margin-top: 0px;
	}

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 76%;
		padding: 0;
		margin-top: 35px;
		
  }

  &__promotext {
    position: relative;
  	margin-top: 83px;
    width: 70%;
    align-self: start;
  }

  &__promo {
    width: 37%;
		position: relative;
		margin-top: 100px;

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: -959px;
      bottom: 15px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 97%;
      object-fit: cover;
    }
  }

  &__lead {
    text-align: left;
		font-size: 24px;
		margin-bottom: 39px;
  }

  &__img {
		
		display: block;
		position: relative;
  }

  &__slogan {
    position: relative;
    display: block;
    width: 79%;
    font-family: "Arkhip", sans-serif;
    font-weight: normal;
    font-size: 54px;
    text-align: left;
    letter-spacing: -1.87px;
    color: var(--color-black);

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: -95px;
      top: -11px;
      height: 100px;
      width: 200px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 37%;
      object-fit: cover;
    }
  }

  &__text {
    font-family: "Corbel", san-serif;
    color: var(--brownish-grey);
    font-size: 21px;
    text-align: left;
    display: block;
    line-height: 28px;
		width: 60%;
		margin: 0;
		margin-top: 28px;
  }

  &__descr {
		font-family: 'Arial' , sans-serif;
    width: 80%;
    display: flex;
    flex-direction: column;
    background-color: var(--color-black);
    position: absolute;
    left: -280px;
    top: 31%;
		border-radius: 14px;
		z-index: 2;
  }

  &__title {
    font-size: 21px;
    padding: 0;
    margin: 0;
    display: block;
    padding-top: 40px;
    padding-left: 105px;
    text-transform: uppercase;
  }

  &__items {
    margin: 0;
		margin-bottom: 60px;
		font-size: 19px;
  }

  &__item {
		font-size: 20px;
    display: block;
    padding: 22px 30px 0 105px;
    line-height: 22px;
    letter-spacing: 0.55px;

    &:first-child {
      padding-top: 41px;
    }
  }
}
